import { Component, OnInit } from '@angular/core';
import { PayrollDerazonaRatePilotService, PayrollDerazonaPekerjaanService } from 'src/app/sdk/payroll/services';
import { Rate_pilot, Pekerjaan } from 'src/app/sdk/payroll/models';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PegawaiDetailService } from '../../../service/pegawai-detail.service';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router } from '@angular/router';
import { FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

    constructor(
        private PayrollDerazonaRatePilotService: PayrollDerazonaRatePilotService,
        private payrollDerazonaPekerjaanService: PayrollDerazonaPekerjaanService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pegawaiDetailService: PegawaiDetailService,
        private pesan: PesanService,
        private router: Router,
    ) { }

    id_pegawai: string = ''
    dataRate: Rate_pilot
    listPekerjaan: Pekerjaan [] = []

    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data
            console.log('id pegawai paja tu ' + this.id_pegawai)
        })
        this.form()
        this.getPreeData()
    }

    getPreeData() {
        this.payrollDerazonaPekerjaanService.getPayrollDerazonaPekerjaans().subscribe(
            data=> {
                this.listPekerjaan = data.data
            }
        )
    }

    dataForm: FormGroupTypeSafe<Rate_pilot>
    form() {
        this.dataForm = this.fb.group<Rate_pilot>({
            id_pekerjaan: new FormControl('', [Validators.required]),
            pc: new FormControl(''),
            sc: new FormControl(''),
            el: new FormControl(''),
            othere: new FormControl(''),
            id_pegawai: new FormControl(this.id_pegawai),
        })
    }

    getValue(key) {
        return this.dataForm.get(key).value
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.dataRate = this.dataForm.value
        this.PayrollDerazonaRatePilotService
            .postPayrollDerazonaRatePilots(this.dataRate)
            .subscribe(
                data => {
                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/pegawai-detail', this.id_pegawai,'payroll','rate-pilot','list'])
    }

}
