import { Component, OnInit } from '@angular/core';
import { PayrollDerazonaRatePilotService, PayrollDerazonaPekerjaanService, PayrollDerazonaDvrService } from 'src/app/sdk/payroll/services';
import { Rate_pilot, Pekerjaan } from 'src/app/sdk/payroll/models';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';
import { PegawaiDetailService } from '../../../service/pegawai-detail.service';
import { NzMessageService } from 'ng-zorro-antd';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { FormGroupTypeSafe, FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { FormControl } from '@angular/forms';

@Component({
    selector: 'app-list',
    templateUrl: './list.component.html',
    styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private payrollDerazonaDvrService: PayrollDerazonaDvrService,
        private pegawaiDetailService: PegawaiDetailService
    ) { }

    id_pegawai: string = ''
    id_cuti_pengajuan: string = ''
    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data

            this.getData()
        })

    }
    date = new Date()
    lisData = []
    getData() {

        let param = {
            id_pegawai: this.id_pegawai,
            bulan: this.date.getMonth() + 1,
            tahun: this.date.getFullYear()
        }
        this.payrollDerazonaDvrService.getPayrollDerazonaDvrs(JSON.stringify(param)).subscribe(
            data => {
                this.lisData = data.data
            }
        )
    }




}
