import { Component, OnInit } from '@angular/core';
import { PayrollDerazonaRatePilotService, PayrollDerazonaPekerjaanService } from 'src/app/sdk/payroll/services';
import { Rate_pilot, Pekerjaan } from 'src/app/sdk/payroll/models';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';
import { PegawaiDetailService } from '../../../service/pegawai-detail.service';
import { NzMessageService } from 'ng-zorro-antd';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { FormGroupTypeSafe, FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { FormControl } from '@angular/forms';

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private payrollDerazonaRatePilotService: PayrollDerazonaRatePilotService,
        private payrollDerazonaPekerjaanService: PayrollDerazonaPekerjaanService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private pegawaiDetailService: PegawaiDetailService,
        private nzMessageService: NzMessageService
    ) { }

    id_pegawai: string = ''
    dataRate: Rate_pilot[] = []
    listPekerjaan: Pekerjaan [] = []

    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data
            console.log('id pegawai paja tu ' + this.id_pegawai)
        })
        this.getRate()
        this.getPreeData()
    }

    getPreeData() {
        this.payrollDerazonaPekerjaanService.getPayrollDerazonaPekerjaans().subscribe(
            data=> {
                this.listPekerjaan = data.data
            }
        )
    }

    getRate() {
        this.spinner.show()
        let param = {
            id_pegawai: this.id_pegawai,
        }
        this.payrollDerazonaRatePilotService.getPayrollDerazonaRatePilots(JSON.stringify(param)).subscribe(
            data => {
                this.dataRate = data.data
                this.spinner.hide()
            }
        )
    }


    edit(id_pekerjaan, id_pegawai) {
        this.dataRate.forEach((item, index) => {
            this.dataRate[index]['edit'] = false;
            if (id_pekerjaan == item.id_pekerjaan) {
                this.dataRate[index]['edit'] = true;
            }
        })
    }

    SimpanUpdate(data,index) {


        this.spinner.show()
        let params: Rate_pilot = {
            id_pekerjaan: data.id_pekerjaan,
            id_pegawai: this.id_pegawai,
            pc: data.pc,
            sc: data.sc,
            el: data.el,
            othere: data.othere,
        }

        this.payrollDerazonaRatePilotService.putPayrollDerazonaRatePilotsId({id:'1', body: params}).subscribe(
            data => {
                this.spinner.hide()

                this.dataRate[index]['edit'] = false;

            },
            err => {
                this.spinner.hide()
                this.pesan.pesanWarningForm('Data tidak valid')
            }
        )

    }

    delete(id) {
        this.spinner.show()
        this.payrollDerazonaRatePilotService
            .deletePayrollDerazonaRatePilotsId(id)
            .subscribe(data => {
                this.getRate()
                this.spinner.hide()
            })

    }

    batalDelete() {
        this.nzMessageService.info('click cancel')
    }



    beforeCreate() {
        this.payrollDerazonaRatePilotService.postPayrollDerazonaRatePilots(this.dataForm.value).subscribe(
            data => {
                this.dataForm.setValue(data.data)
                this.getRate()
            },
            err => {
                this.pesan.pesanWarningForm('Gagal di load data')
            },
            () => {
                console.log('HTTP request completed.')
            },
        )
    }

    dataForm: FormGroupTypeSafe<Rate_pilot>
    form() {
        this.dataForm = this.fb.group<Rate_pilot>({
            id_pegawai: new FormControl(this.id_pegawai),
            id_rate_pilot: new FormControl(''),
        })
    }


}
